ow:hidden;margin:0 auto 35px}.about__leadership-img{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%,-50%);-ms-transform:translate(-50%,-50%);transform:translate(-50%,-50%)}.about__leadership-title{font-size:25px;line-height:29px;color:#3a9b3f;margin-bottom:8px}.about__leadership-sub-title{font-family:HelveticaRegular,sans-serif;font-size:17px;line-height:21px;color:#012f00}.about__leadership-sub-text{font-family:HelveticaRegular,sans-serif;font-size:18px;line-height:21px;margin-top:30px}