.drop-menu{position:relative}.drop-menu__button{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;cursor:pointer}.drop-menu__name{font-family:HelveticaNeueBold,sans-serif;font-size:13px;line-height:16px;color:#fff;text-transform:uppercase;text-decoration:none;-webkit-transition:all .2s ease-out;-o-transition:all .2s ease-out;transition:all .2s ease-out}.drop-menu__arrow{width:0;height:0;border-left:5px solid rgba(0,0,0,0);border-right:5px solid rgba(0,0,0,0);border-top:8px solid #fff;margin-left:14px}.drop-menu__counter{position:absolute;bottom:100%;right:0;font-family:HelveticaNeueBold,sans-serif;font-size:13px;line-height:16px;color:#ffc217}.drop-menu__drop{position:absolute;top:calc(100% + 30px);right:0;min-width:290px;max-height:calc(100vh - 100px);background:#688c34;border-radius:5px;overflow-y:auto;-webkit-transition:all .2s ease-out;-o-transition:all .2s ease-out;transition:all .2s ease-out;opacity:0;visibility:hidden;-webkit-transform:translateY(20px);-ms-transform:translateY(20px);transform:translateY(20px)}.drop-menu__drop--active{opacity:1;visibility:visible;-webkit-transform:translate(0);-ms-transform:translate(0);transform:translate(0)}.drop-menu__drop-title{color:#ffc217}.drop-menu__drop-title,.drop-menu__main-link{display:block;font-family:HelveticaNeueBold,sans-serif;font-size:17px;line-height:21px;padding:28px 35px}.drop-menu__main-link{color:#fff;border-top:1px solid #79ac2f;-webkit-transition:all .2s ease-out;-o-transition:all .2s ease-out;transition:all .2s ease-out}.drop-menu__main-link .count{position:relative;top:-10px;font-family:HelveticaNeueBold,sans-serif;font-size:13px;line-height:16px;color:#ffc217;margin-left:18px}.drop-menu__main-link--active,.drop-menu__main-link:hover{background:#fff;color:#012f00}