.lotteries-page{display:grid;grid-template-columns:repeat(5,1fr);grid-gap:0 20px;padding:40px 60px 25px;margin-top:10px;background:#fff;-webkit-box-shadow:0 3px 6px rgba(0,0,0,.16);box-shadow:0 3px 6px rgba(0,0,0,.16)}@media (max-width:1199px){.lotteries-page{grid-template-columns:repeat(auto-fit,minmax(230px,1fr))}}@media (max-width:991px){.lotteries-page{padding:30px 30px 20px}}@media (max-width:600px){.lotteries-page{padding:0;background-color:rgba(0,0,0,0);-webkit-box-shadow:none;box-shadow:none;grid-template-columns:repeat(auto-fit,minmax(190px,1fr))}}