.content{margin-top:35px;padding:70px 115px 80px;background:#fff;-webkit-box-shadow:0 3px 6px rgba(0,0,0,.16);box-shadow:0 3px 6px rgba(0,0,0,.16)}@media (max-width:1199px){.content{padding:50px 40px 60px}}@media (max-width:600px){.content{padding:0;background-color:rgba(0,0,0,0);-webkit-box-shadow:none;box-shadow:none;margin-top:30px}}.content h4{font-size:20px;line-height:25px;color:#688c34;margin-bottom:18px}.content p{font-size:18px;line-height:21px;color:#000;margin-bottom:18px;min-height:1px}@media (max-width:600px){.content p{font-size:17px;line-height:20px}}