.careers{position:relative;display:grid;grid-template-columns:minmax(400px,1fr) minmax(400px,600px);grid-gap:0 130px;background:#fff;margin-top:20px;padding:50px 55px 120px 115px;-webkit-box-shadow:0 3px 6px rgba(0,0,0,.16);box-shadow:0 3px 6px rgba(0,0,0,.16)}@media (max-width:1199px){.careers{padding:50px 55px;grid-gap:0 50px}}@media (max-width:991px){.careers{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:reverse;-ms-flex-direction:column-reverse;flex-direction:column-reverse}}@media (max-width:767px){.careers{padding:0;-webkit-box-shadow:none;box-shadow:none;background-color:rgba(0,0,0,0)}}.careers__item:not(:last-child){margin-bottom:40px}@media (min-width:991px){.careers__item:not(:last-child){margin-bottom:calc(40px + (6000vw - 59460px)/209)}}@media (min-width:1200px){.careers__item:not(:last-child){margin-bottom:100px}}.careers__item-title{font-size:40px;line-height:45px;color:#3a9b3f;margin-bottom:20px}@media (min-width:767px){.careers__item-title{margin-bottom:calc(20px + (3000vw - 23010px)/433)}}@media (min-width:1200px){.careers__item-title{margin-bottom:50px}}@media (max-width:600px){.careers__item-title{font-size:25px;line-height:29px}}.careers__item-title--mobile{display:none}@media (max-width:991px){.careers__item-title--mobile{display:block}}@media (max-width:991px){.careers__item-title--mobile-hide{display:none}}.careers__item-text,.careers__item-text p{font-size:18px;line-height:21px;color:#000}@media (max-width:600px){.careers__item-text,.careers__item-text p{font-size:17px;line-height:20px}}@media (max-width:991px){.careers__image-wrapper{margin-bottom:30px}}.careers__image{max-width:100%}.careers__link{position:relative;font-family:HelveticaNeueBold,sans-serif;font-size:22px;line-height:26px;color:#3a9b3f;text-decoration:underline}@media (max-width:600px){.careers__link{font-size:17px;line-height:20px}}