.burger-menu{position:relative;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;outline:none;border:none;min-height:18px;background:none;display:-webkit-box;display:-ms-flexbox;display:flex;cursor:pointer}.burger-menu--mobile{display:none}@media (max-width:991px){.burger-menu{display:-webkit-box;display:-ms-flexbox;display:flex}}.burger-menu__item{width:30px;height:5px;background:#000;-webkit-transition:all .12s ease-out;-o-transition:all .12s ease-out;transition:all .12s ease-out;margin-bottom:2px;border-radius:10px}.burger-menu--active .burger-menu__item,.burger-menu__item:last-child{margin-bottom:0}.burger-menu--active .burger-menu__item:nth-child(2){opacity:0;visibility:hidden}.burger-menu--active .burger-menu__item:first-child{-webkit-transform:rotate(45deg) translate(7px,6px);-ms-transform:rotate(45deg) translate(7px,6px);transform:rotate(45deg) translate(7px,6px)}.burger-menu--active .burger-menu__item:last-child{-webkit-transform:rotate(-45deg) translate(1px);-ms-transform:rotate(-45deg) translate(1px);transform:rotate(-45deg) translate(1px)}