.heading{text-align:center;color:#012f00}.heading__title{font-family:HelveticaNeueBlack,sans-serif;text-align:center;font-size:26px}@media (min-width:768px){.heading__title{font-size:calc(26px + (700vw - 5376px)/256)}}@media (min-width:1024px){.heading__title{font-size:33px}}.heading__title{line-height:36px}@media (min-width:768px){.heading__title{line-height:calc(36px + (1000vw - 7680px)/256)}}@media (min-width:1024px){.heading__title{line-height:46px}}.heading__title span{color:#688c34}.heading__title--white{color:#fff}.heading__description{color:#222;font-size:15px}@media (min-width:768px){.heading__description{font-size:calc(15px + (300vw - 2304px)/256)}}@media (min-width:1024px){.heading__description{font-size:18px}}.heading__description{line-height:21px}@media (min-width:768px){.heading__description{line-height:calc(21px + (400vw - 3072px)/256)}}@media (min-width:1024px){.heading__description{line-height:25px}}.heading__link{position:relative;color:#219653;-webkit-transition:all .2s ease-out;-o-transition:all .2s ease-out;transition:all .2s ease-out;font-size:15px}@media (min-width:768px){.heading__link{font-size:calc(15px + (300vw - 2304px)/256)}}@media (min-width:1024px){.heading__link{font-size:18px}}.heading__link{line-height:21px}@media (min-width:768px){.heading__link{line-height:calc(21px + (400vw - 3072px)/256)}}@media (min-width:1024px){.heading__link{line-height:25px}}.heading__link:before{position:absolute;top:100%;left:0;content:"";width:100%;height:1px;background:#219653;-webkit-transition:all .2s ease-out;-o-transition:all .2s ease-out;transition:all .2s ease-out}.heading__link:hover{color:#36d379}.heading__link:hover:before{background:#36d379}