.my-account{position:relative}.my-account__user{display:-webkit-box;display:-ms-flexbox;display:flex;font-family:HelveticaRegular,sans-serif;font-size:13px;line-height:16px;color:#fff}.my-account__user--mobile{display:none;color:#000;margin-right:auto;margin-left:20px}@media (max-width:991px){.my-account__user--mobile{display:block}}@media (max-width:390px){.my-account__user--mobile{margin-left:10px}}.my-account__user--mobile .my-account__user-name,.my-account__user--mobile .my-account__user-price{color:#000}@media (max-width:390px){.my-account__user--mobile .my-account__user-price{display:none}}.my-account__user-name{font-family:HelveticaRegular,sans-serif;font-size:13px;line-height:16px;color:#fff}.my-account__user-price{font-family:HelveticaNeueBold,sans-serif;font-size:13px;line-height:16px;color:#fff;margin-left:10px}.my-account__dropdown{position:absolute;top:calc(100% + 30px);left:0;width:335px;background:#fff;padding:30px 10px 28px;border-radius:4px;-webkit-box-shadow:0 3px 6px rgba(0,0,0,.16);box-shadow:0 3px 6px rgba(0,0,0,.16);-webkit-transition:all .25s ease-out;-o-transition:all .25s ease-out;transition:all .25s ease-out;opacity:0;visibility:hidden;-webkit-transform:translateY(25px);-ms-transform:translateY(25px);transform:translateY(25px)}.my-account__dropdown--active{opacity:1;visibility:visible;-webkit-transform:translate(0);-ms-transform:translate(0);transform:translate(0)}.my-account__dropdown-head{border-bottom:1px solid #c7c7c7;padding-left:25px;padding-bottom:20px}.my-account__title{margin-bottom:40px}.my-account__title-name{font-family:HelveticaNeueBold,sans-serif;font-size:30px;line-height:34px;color:#000;margin-bottom:5px}.my-account__title-name-edit{font-family:HelveticaNeueBold,sans-serif;font-size:18px;line-height:22px;color:#3a9b3f}.my-account__info-item:not(:last-child){margin-bottom:40px}.my-account__info-label{font-family:HelveticaRegular,sans-serif;font-size:15px;line-height:20px;color:#707070;margin-bottom:10px}.my-account__info-name{font-family:HelveticaNeueBold,sans-serif;font-size:18px;line-height:22px;color:#012f00}.my-account__dropdown-foot{padding:28px 25px 0}.my-account__wallet{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;margin-bottom:25px}.my-account__wallet-label{font-family:HelveticaNeueBold,sans-serif;font-size:18px;line-height:22px;color:#012f00;margin-bottom:20px}.my-account__wallet-sum{font-family:HelveticaNeueBold,sans-serif;font-size:30px;line-height:34px;color:#6d9138}